import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  margin: 0;
  text-align: center;
  display: inline-block;
`

const Left = styled.div`
  float: left;
`

const Right = styled.div`
  float: right;
`

const SubjectHeader = styled.div`
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 20px;
  font-weight: bold;
`


const SubjectImage = styled.img`
  width: 100px;
`

const Subject = props => {
  return (
      <Wrapper>
        <Left>
          <SubjectImage src={props.src}/>
        </Left>
        <Right>
          <SubjectHeader>{props.name}</SubjectHeader>
        </Right>

      </Wrapper>
  )
}

export default Subject